* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  color: #333;
  background-color: #f9fafc;
}

.app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.header {
  color: #fff;
  z-index: 1000;
  background: linear-gradient(90deg, #ff5722, #ff8a50);
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 6px #00000026;
}

.logo-container img {
  cursor: pointer;
  border-radius: 10px;
  width: auto;
  height: 65px;
}

.nav-Item ul {
  gap: 25px;
  list-style: none;
  display: flex;
}

.nav-Item li {
  cursor: pointer;
  font-weight: 500;
  transition: transform .2s, color .3s;
}

.nav-Item li:hover {
  color: #ffe0b2;
  transform: translateY(-2px);
}

.login {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(45deg, #ff5722, #ff8a50);
  border: none;
  border-radius: 25px;
  padding: 8px 20px;
  font-size: 15px;
  font-weight: 600;
  transition: background .3s, transform .2s;
  position: relative;
  top: -8px;
}

.body {
  flex-direction: column;
  flex: 1;
  gap: 20px;
  padding: 25px 40px;
  display: flex;
}

.filter {
  z-index: 999;
  background: #fff;
  border-radius: 50px;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  max-width: 900px;
  margin: 0 auto;
  padding: 12px 18px;
  display: flex;
  position: sticky;
  top: 80px;
  box-shadow: 0 3px 8px #00000014;
}

.search-box {
  border: 1.5px solid #e0e0e0;
  border-radius: 30px;
  outline: none;
  flex: 1;
  min-width: 150px;
  padding: 10px 16px;
  font-size: 15px;
  transition: border-color .3s, box-shadow .3s;
}

.search-box:focus {
  border-color: #ff5722;
  box-shadow: 0 0 4px #ff572266;
}

.button-group {
  gap: 6px;
  display: flex;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(45deg, #ff5722, #ff8a50);
  border: none;
  border-radius: 25px;
  padding: 10px 16px;
  font-size: 15px;
  font-weight: 600;
  transition: all .3s;
}

.search-btn:hover {
  background: linear-gradient(45deg, #e64a19, #ff7043);
  transform: translateY(-2px);
}

.filter-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(45deg, #4caf50, #81c784);
  border: none;
  border-radius: 25px;
  padding: 10px 16px;
  font-size: 15px;
  font-weight: 600;
  transition: all .3s;
}

.filter-btn:hover {
  background: linear-gradient(45deg, #388e3c, #66bb6a);
  transform: translateY(-2px);
}

.res-container {
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  gap: 30px;
  margin-top: 30px;
  display: grid;
}

.res-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 16px;
  margin: 5px;
  padding: 20px;
  transition: transform .2s, box-shadow .2s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 6px 16px #0000001f;
}

.res-card:before {
  content: "";
  background: radial-gradient(circle at 0 0, #ff572214, #0000 70%);
  width: 200%;
  height: 200%;
  position: absolute;
  top: -50%;
  left: -50%;
  transform: rotate(25deg);
}

.res-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 22px #0000002e;
}

.res-card img {
  object-fit: cover;
  border-radius: 12px;
  width: 100%;
  height: 150px;
  margin-bottom: 12px;
}

.res-card h3 {
  color: #222;
  margin-bottom: 8px;
  font-size: 18px;
}

.res-card p {
  color: #555;
  margin-bottom: 5px;
  font-size: 14px;
}

.order-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(45deg, #ff5722, #ff8a50);
  border: none;
  border-radius: 25px;
  width: 100%;
  margin-top: 10px;
  padding: 10px 14px;
  font-size: 15px;
  font-weight: 600;
  transition: background .3s, transform .2s;
}

.order-button:hover {
  background: linear-gradient(45deg, #e64a19, #ff7043);
  transform: scale(1.05);
}

@keyframes shimmer {
  0% {
    background-position: -400px 0;
  }

  100% {
    background-position: 400px 0;
  }
}

.shimmer-container {
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  gap: 30px;
  margin-top: 30px;
  padding: 20px;
  display: grid;
}

.shimmer-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 16px;
  padding: 20px;
  animation: .3s ease-in-out fadeIn;
  overflow: hidden;
  box-shadow: 0 6px 16px #0000001f;
}

.shimmer-img {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 800px 100%;
  border-radius: 12px;
  width: 100%;
  height: 150px;
  margin-bottom: 16px;
  animation: 1.6s linear infinite shimmer;
}

.shimmer-content {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.shimmer-title {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 800px 100%;
  border-radius: 6px;
  width: 70%;
  height: 18px;
  animation: 1.6s linear infinite shimmer;
}

.shimmer-line {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 800px 100%;
  border-radius: 6px;
  width: 100%;
  height: 12px;
  animation: 1.6s linear infinite shimmer;
}

.shimmer-line.short {
  width: 80%;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width <= 600px) {
  .header {
    text-align: center;
    flex-direction: column;
    gap: 15px;
    padding: 15px 20px;
  }

  .nav-Item ul {
    gap: 15px;
  }

  .login {
    top: 0;
  }

  .body {
    padding: 15px 20px;
  }

  .filter {
    flex-direction: column;
    gap: 15px;
    padding: 15px;
    top: 85px;
  }

  .search-box {
    width: 100%;
  }

  .button-group {
    justify-content: center;
    width: 100%;
  }

  .search-btn, .filter-btn {
    flex: 1;
    padding: 12px;
  }

  .res-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

@media (width >= 601px) and (width <= 900px) {
  .header {
    padding: 15px 30px;
  }

  .body {
    padding: 20px 30px;
  }

  .filter {
    padding: 10px 15px;
  }

  .search-box {
    flex: 2;
  }

  .button-group {
    flex: 1;
  }

  .res-container {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 25px;
  }
}

.nav-link {
  color: #fff;
  text-decoration: none;
}

.menu {
  background: linear-gradient(135deg, #fdfcfb, #e2d1c3);
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  animation: 1s ease-in-out fadeIn;
  display: flex;
}

.menu > div:first-child {
  text-align: center;
  backdrop-filter: blur(12px);
  background: #ffffffd9;
  border: 1px solid #fff6;
  border-radius: 1.5rem;
  width: 100%;
  max-width: 42rem;
  margin-bottom: 2rem;
  padding: 2.5rem;
  transition: transform .3s, box-shadow .3s;
  animation: .8s slideUp;
  box-shadow: 0 8px 24px #0000001f;
}

.menu > div:first-child:hover {
  transform: translateY(-6px)scale(1.02);
  box-shadow: 0 12px 32px #0000002e;
}

.menu h1 {
  color: #1f2937;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #ff5722, #ff8a50);
  -webkit-background-clip: text;
  margin-bottom: .6rem;
  font-size: 2.5rem;
  font-weight: 800;
}

.menu p {
  color: #4b5563;
  font-size: 1.125rem;
  line-height: 1.5;
}

.restaurant-info {
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 1.2rem;
  display: flex;
}

.restaurant-info h2 {
  color: #374151;
  align-items: center;
  gap: .5rem;
  font-size: 1.25rem;
  font-weight: 600;
  display: flex;
}

.restaurant-info h2:first-child {
  color: #16a34a;
  animation: 1.8s infinite pulse;
}

.menu-list {
  backdrop-filter: blur(10px);
  background: #ffffffe6;
  border: 1px solid #e5e7eb99;
  border-radius: 1.5rem;
  width: 100%;
  max-width: 42rem;
  padding: 2.5rem;
  animation: 1.2s fadeIn;
  box-shadow: 0 6px 20px #0000001a;
}

.menu-list h2 {
  color: #1f2937;
  margin-bottom: 1.2rem;
  font-size: 2rem;
  font-weight: 700;
  position: relative;
}

.menu-list h2:after {
  content: "";
  background: linear-gradient(90deg, #ff5722, #ff8a50);
  border-radius: 2px;
  width: 60px;
  height: 4px;
  position: absolute;
  bottom: -6px;
  left: 0;
}

.menu-list p {
  color: #555;
  font-size: 1.05rem;
  line-height: 1.6;
  animation: 1s slideUp;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: .8;
    transform: scale(1.1);
  }
}

@media (width <= 768px) {
  .menu {
    padding: 1rem;
  }

  .menu > div:first-child, .menu-list {
    border-radius: 1rem;
    padding: 1.5rem;
  }

  .menu h1 {
    font-size: 2rem;
  }

  .restaurant-info {
    flex-direction: column;
    gap: 1rem;
  }
}
